import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const form = document.querySelector('.form');

form.addEventListener('submit', handleSubmit);

function handleSubmit(e) {
  e.preventDefault();

  const delayInput = form.querySelector('input[name="delay"]');
  const stateRadio = form.querySelector('input[name="state"]:checked');

  if (!stateRadio) {
    iziToast.error({
      position: 'topRight',
      title: 'Error',
      message: 'Please select a state',
      backgroundColor: '#EF4040',
    });
    return;
  }

  const delay = Number(delayInput.value);
  const isFulfilled = stateRadio.value === 'fulfilled';

  new Promise((resolve, reject) => {
    setTimeout(() => {
      isFulfilled ? resolve(delay) : reject(delay);
    }, delay);
  })
    .then(delay => showMessage(true, delay))
    .catch(delay => showMessage(false, delay));
}

function showMessage(isSuccess, delay) {
  iziToast.show({
    position: 'topRight',
    title: isSuccess ? '✅ Success' : '❌ Error',
    message: isSuccess
      ? `Promise виконано за ${delay}ms`
      : `Promise відхилено за ${delay}ms`,
    backgroundColor: isSuccess ? '#59A10D' : '#EF4040',
  });
}
